<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
        <el-form :inline="true" :model="searchValidateForm" ref="searchValidateForm" label-width="100px" class="search-validate-form" :rules="searchValidateFormRules">
          <!-- 请选择景区 start -->
          <el-form-item label="" prop="spotId">
            <el-select v-model="searchValidateForm.spotId" filterable placeholder="请选择景区" clearable>
              <el-option v-for="(item, index) in searchScenicOptions" :key="index" :label="item.SCENIC_SPOT_NAME" :value="item.SCENIC_SPOT_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请输入外设名称 start -->
          <el-form-item label="" prop="peripheralName">
            <el-input v-model="searchValidateForm.peripheralName" placeholder="请输入外设名称"></el-input>
          </el-form-item>
          <!-- 请输入外设名称 end -->

          <!-- 请选择日期统计方式 start -->
          <!-- <el-form-item label="" prop="selectDateState">
            <el-select v-model="selectDateState" filterable placeholder="请选择日期统计方式" @change="onDateChange">
              <el-option v-for="(item, index) in dateStateOptions" :key="index" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item> -->
          <!-- 请选择日期统计方式 end -->

          <!-- 输入起止时间 start -->
          <el-form-item label="" prop="searchStartEndDate">
            <el-date-picker v-model="searchStartEndDate" unlink-panels :type="dateType" range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" :clearable="false">
            </el-date-picker>
          </el-form-item>
          <!-- 输入起止时间 end -->
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 搜索 start -->
        <el-button type="primary" icon="el-icon-search" @click="onSearch('searchValidateForm')">搜索</el-button>
        <!-- 搜索 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset">重置</el-button>
        <!-- 重置 end -->

        <!-- 新增 start -->
        <el-button type="primary" icon="el-icon-circle-plus-outline" @click="onAdd">新增</el-button>
        <!-- 新增 end -->

        <!-- 导出数据 start -->
        <el-button type="primary" :loading="downloadLoading" icon="el-icon-download" @click="exportExcel">导出数据</el-button>
        <!-- 导出数据 end -->

				<!-- 导入数据 -->
				<el-upload
				action="/system/robotPeripheral/importExcel"
				:show-file-list="false" 
				:on-change="handleChange" 
				ref="upload" 
				:file-list="fileList"
				:on-remove="handleRemove"
				:on-success="handleImportSuccess"
				:on-exceed="handleExceed"
				 >
					<el-button type="primary" :ioading="importLoading" icon="el-icon-upload2" @click="importExcel">导入数据</el-button>
				</el-upload>
				<!-- 导入数据 -->

        <!-- 导出报表 start -->
        <!-- <a href="" id="exportReport" download="机器人效率分析">
          <el-button type="primary" icon="el-icon-camera-solid" @click="onExportReport">导出报表</el-button>
        </a> -->
        <!-- 导出报表 end -->
      </el-header>
      <!-- 显示表格 start -->
      <el-main>
        <!-- 表格 start -->
        <el-table class="table" :data="tableData" style="width: 100%" border v-loading="tableLoading" :header-cell-style="{
                background: '#f2f2f2',
                color: '#606266',
                height: '38px',
                padding: '0px'
              }" :cell-style="{ height: '38px', padding: '0' }" :default-sort="{ prop: 'grossProfit', order: 'descending' }" @sort-change="onSortChange">
          <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" align="center" min-width="130" :sortable="tableSortable(value)">
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
            </template>
          </el-table-column>

          <el-table-column label="操作" min-width="130">
            <template slot-scope="scope">
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="text" size="small">
                编辑
              </el-button>
							<el-button @click.native.prevent="deleteRow(scope.$index, scope.row)" type="text" size="small">
							  删除
							</el-button>
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table id="outTable" class="table" :data="exportTableData" style="width: 100%;display: none;" border>
          <el-table-column v-for="(value, key) in tableFields" :key="key" :prop="value" :label="key" min-width="130" align="center">
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>
      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination background layout="prev, pager, next, total, sizes, jumper" :total="total" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" @current-change="onCurrentChange" @size-change="onSizeChange">
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>

      <!-- dialog对话框 start -->
      <el-dialog class="dialog" :title="dialogTitle + '机器人外设成本'" :visible.sync="dialogVisible" :before-close="handleClose" :close-on-click-modal="false">
        <!-- form表单 start -->
        <el-form :model="validateForm" ref="validateForm" label-width="100px" class="demo-ruleForm" :rules="validateFormRules">
          <!-- 请选择景区 start -->
          <el-form-item label="景区名称" prop="scenicSpotId">
            <el-select v-model="validateForm.scenicSpotId" filterable placeholder="请选择景区" clearable @change="onChangeSpot" style="width: 100%;">
              <el-option v-for="(item, index) in scenicOptions" :key="index" :label="item.SCENIC_SPOT_NAME" :value="item.SCENIC_SPOT_ID">
              </el-option>
            </el-select>
          </el-form-item>
          <!-- 请选择景区 end -->

          <!-- 请选择外设单位 start -->
          <el-form-item label="外设单位" prop="peripheralCompany">
            <el-input v-model="validateForm.peripheralCompany" placeholder="请输入外设单位"></el-input>
          </el-form-item>
          <!-- 请选择外设单位 end -->

          <!-- 请输入外设名称 start -->
          <el-form-item label="外设名称" prop="peripheralName">
            <el-input v-model="validateForm.peripheralName" placeholder="请输入外设名称"></el-input>
          </el-form-item>
          <!-- 请输入外设名称 end -->

          <!-- 请输入外设单价 start -->
          <el-form-item label="外设单价/元" prop="peripheralPrice">
            <el-input v-model="validateForm.peripheralPrice" placeholder="请输入外设单价/元"></el-input>
          </el-form-item>
          <!-- 请输入外设单价 end -->

          <!-- 请输入外设数量 start -->
          <el-form-item label="外设数量" prop="peripheralQuantity">
            <el-input v-model.number="validateForm.peripheralQuantity" placeholder="请输入外设数量"></el-input>
          </el-form-item>
          <!-- 请输入外设数量 end -->

          <!-- 请输入备注 start -->
          <el-form-item label="备注" prop="remarks">
            <el-input v-model="validateForm.remarks" placeholder="请输入备注"></el-input>
          </el-form-item>
          <!-- 请输入备注 end -->
        </el-form>
        <!-- form 表单 end -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
          <el-button @click="dialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- dialog对话框 end -->

      <!-- <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="表格" name="table">
          
        </el-tab-pane>
        <el-tab-pane label="图表" name="echarts">
          <div class="my-echarts">
            <div id="myChart"></div>
          </div>
        </el-tab-pane>
      </el-tabs> -->
      <!-- 显示表格 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatDateTime, formatMonth } from '../utils/utils.js'
export default {
  name: 'SettlementFlow',
  data () {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (!Number(value)) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod (name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          console.log(value);
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      showTable: true, // 是否显示表格
      feeInput: '', // 请输入手续费
      taxInput: '', // 请输入税点
      searchFeeInput: '', // 搜索的手续费
      searchTaxInput: '', // 搜索的税点
      tableFields: {
        //导出Excel表格的表头设置
        创建时间: 'createDate',
        景区名称: 'scenicSpotName',
        外设名称: 'peripheralName',
        '外设单价/元': 'peripheralPrice',
        '外设数量': 'peripheralQuantity',
        '外设单位': 'peripheralCompany',
        '备注': 'remarks'
      },
      tableData: [], // 表格数据
      tableLoading: false,

      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      scenicOptions: [], // 提交的景区选项

      robotOptions: [], // 提交时的机器人选项
      companyOptions: [], // 提交时的公司选项

      dateStateOptions: [
        {
          value: '1',
          label: '按年查询'
        },
        {
          value: '2',
          label: '按月查询'
        },
        {
          value: '3',
          label: '按日查询'
        }
      ], // 日期统计方式选项

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画
			fileList: [],//导入excel列表
      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数
      total: 0, // 总页数

      dialogVisible: false, // 是否显示编辑dialog对话框

      dialogState: '', // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

      selectDateState: '3', // 日期统计方式（年，月，日）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd HH:mm:ss', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象

      validateForm: {}, // 表单验证对象

      // searchStartEndDate:[new Date(),new Date()], // 搜索的起止时间
			searchStartEndDate:'', // 搜索的起止时间	

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区

      downloadUrl: '',

      myChart: null, // 左侧饼图echarts实例
      myLineChart: null, // 左侧饼图echarts实例
      markLineData: [], // 标线的数据数组
      option: {}, // 左侧饼图optino
      statisticsStateList: [], // 饼图数据

      dialogTitle: '',

      robotId: '', // 当前点击行的 robotId
      statisticDialogVisible: false, // 设备统计dialgo显示状态

      dispatchTrackList: [], // 调运轨迹列表
      dispatchTrackDialogVisible: false, // 调运轨迹dialgo显示状态
      dispatchTrailCurrentPage: 1, // 调运轨迹当前页数
      dispatchTrailTotal: 0, // 调运轨迹总条数
      dispatchTrailPageSize: 10, // 调运轨迹每页多少条
      currentRobotCode: '', // 当前点击行的机器人编号

      errorRecordsList: [], // 故障记录列表

      sumTotal: 0, // 机器人总数量
      nowsumTotal: 0, // 机器人总在线数量
      saturationTotal: 0, // 机器人总运营饱和度

      activeName: 'table', // tabs 显示状态

      // 搜索表单规则
      searchValidateFormRules: {},

      tableSort: '1,desc', // 表格排序

      // 提交表单规则
      validateFormRules: {
        // 景区名称
        scenicSpotId: [
          {
            validator: checkRequireMethod('景区名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 外设单位
        peripheralCompany: [
          {
            validator: checkRequireMethod('外设单位'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 外设名称
        peripheralName: [
          {
            validator: checkRequireMethod('外设名称'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 外设单价
        peripheralPrice: [
          {
            validator: checkRequireNumberMethod('外设单价/元'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],

        // 外设数量
        peripheralQuantity: [
          {
            validator: checkRequireMethod('外设数量'),
            required: true,
            trigger: ['blur', 'change']
          }
        ],
      }
    }
  },
  created () {
    this.getSearchValidateForm()
    this.getValidateForm()
    this.login()
    this.getCompanyList()
    this.getScenicList()
    this.initTableData()
    // this.getRobotIdList()
  },
  beforeMount () { },
  mounted () {
    // this.getSpotGrossProfitMarginChartList()
    // this.drawEcharts()
  },
  methods: {
    // 获取搜索表单验证对象
    getSearchValidateForm () {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.searchValidateForm = {
        spotId: '', // 景区id
				// scenicSpotId:'',
        // ...validateForm
      }
    },

    // 获取提交表单验证对象
    getValidateForm () {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ''
      }
      this.validateForm = {
        ...validateForm,
        robotPeripheralId: '',
        scenicSpotId: '',
      }
    },

    // 登录(必须)
    async login () {
      const { data: res } = await this.$http.post(
        '/loginSystem?username=lijiazhao&password=lijiazhao'
      )
    },

    /**
     * 根据景区ID获取公司列表（搜索时公司列表）
     */
    async getCompanyList (spotId) {
      if (!spotId) {
        spotId = null
      }
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/companyList',
        {
          params: {
            spotId: spotId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res)
        this.searchCompanyOptions = res.data
        this.companyOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 根据公司ID获取景区列表（搜索时景区列表）
     */
    async getScenicList (companyId) {
      if (!companyId) {
        companyId = null
      }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get(
        '/system/robot_time_statistics/spotList',
        {
          params: {
            companyId: companyId
          }
        }
      )
      if (res.state == '200') {
        // console.log(res);
        this.searchScenicOptions = res.data
        this.scenicOptions = res.data
        return res
      } else {
        return
      }
    },

    /**
     * 初始化表格数据
     */
    async initTableData () {
      this.tableLoading = true

      var beginDate = ''
      var endDate = ''
			// console.log(this.searchStartEndDate)
      if (this.searchStartEndDate.length !== 0) {
        beginDate = formatDateTime(
          this.searchStartEndDate[0],
					this.selectDateState
        )
        endDate = formatDateTime(
          this.searchStartEndDate[1],
					this.selectDateState
        )
      }
			// else{
			// 	this.$message({
			// 	  type: 'error',
			// 	  message: '请选择时间！'
			// 	})
			// }
//表格接口数据
      const { data: res } = await this.$http.get(
        '/system/robotPeripheral/getRobotPeripheral',
        {
          params: {
            ...this.searchValidateForm,
            // beginDate:'2022-11-12',
            // endDate:'2022-11-13',
						// beginDate:beginDate +''+'00:00:00',
						// endDate:endDate +' ' + '23:00:00',
						beginDate,
						endDate,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      console.log(res)
      if (res.state != 200) {
        this.$message({
          type: 'error',
          message: '请求失败！'
        })
        return
      }
      this.tableData = res.data
      this.total = res.total
      // var type = JSON.parse(res.type)
      // this.sumTotal = type.sum
      // this.nowsumTotal = type.nowsum
      // this.saturationTotal = type.saturation

      this.tableLoading = false

      // var resultData = res.data.concat()
      // resultData.forEach((item, index) => {
      //   if (this.searchVF.type == '2' && item.订单状态 == '实际收入金额') {
      //     resultData.unshift(resultData.splice(index, 1)[0])
      //   } else {
      //     if (item.订单状态 == '已付款') {
      //       resultData.unshift(resultData.splice(index, 1)[0])
      //     }
      //   }
      // })

      // var source = resultData.map((item) => {
      //   return [item.订单状态, item.订单数量]
      // })

      // source.unshift(['order', '已付款订单', '其他订单'])
      // this.addOtherData(source, 5)
      // this.option.dataset.source = source
      // this.option.series[0].markLine.data = this.markLineData
      // this.myChart.setOption(this.option)
    },

    /**
     * 获取机器人利用率图表数据
     */
    async getSpotGrossProfitMarginChartList () {
      var startTime = null
      var endTime = null

      if (this.searchStartEndDate.length !== 0) {
        startTime = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endTime = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/efficiencyAnalysis/getSpotGrossProfitMarginChartList',
        {
          params: {
            ...this.searchValidateForm,
            type: this.selectDateState,
            startTime,
            endTime,
            sort: this.tableSort,
            pageNum: this.currentPage, // 页码
            pageSize: this.pageSize // 每页条数
          }
        }
      )
      console.log(res)
      // if (res.code != 200) {
      //   this.$message({
      //     type: 'error',
      //     message: '请求失败！'
      //   })
      //   return
      // }

      this.option.xAxis.data = res.data.dateTime
      this.option.series[0].data = res.data.utilizationRate
      this.option.series[1].data = res.data.yearOnYear
      this.option.series[2].data = res.data.monthOnMonth
      this.myChart.setOption(this.option)
    },

    /**
     * 新增表格数据
     */
    async addTableData () {
			console.log(this.validateForm)
      const { data: res } = await this.$http.post(
        '/system/robotPeripheral/addRobotPeripheral',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
        this.$refs.validateForm.resetFields() // 重置表单
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 修改表格数据
     */
    async editTableData () {
      const { data: res } = await this.$http.post(
        '/system/robotPeripheral/editRobotPeripheral',
        this.validateForm
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 删除表格数据
     */
    async delTableData (softAssetInformationId) {
      console.log(softAssetInformationId)
      const { data: res } = await this.$http.delete(
        '/system/robotSoftAssetInformation/delRobotSoftAssetInformation',
        {
          params: {
            softAssetInformationId
          }
        }
      )
      if (res.state == '200') {
        this.$message({
          message: res.msg,
          type: 'success',
          offset: 100,
          center: true,
          duration: 1000
        })
        this.initTableData()
      } else {
        this.$message({
          message: res.msg,
          type: 'error',
          offset: 100,
          center: true,
          duration: 3000
        })
      }
    },

    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData () {
      var beginDate = null
      var endDate = null
			// console.log(this.exportTableData)
      if (this.searchStartEndDate.length !== 0) {
				// console.log(this.searchStartEndDate)
        beginDate = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
				
        endDate = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }
			console.log(beginDate,endDate)
			var data = {
				peripheralName:this.searchValidateForm.peripheralName,
				spotId:this.searchValidateForm.spotId,
				beginDate:beginDate,
				endDate:endDate,
			}
      const { data: res } = await this.$http.post(
        '/system/robotPeripheral/uploadExcelRobotPeripheral',this.$qs.stringify(data),
     //    {
     //      ...this.searchValidateForm,
					// // scenicSpotId:
					// // beginDate:'2022-11-12 13:50:37',
					// // endDate:'2022-11-12 13:50:39',
					// // beginDate:startTime+''+'00:00:00',
					// // endDate:endTime+''+'23:00:00',
					// beginDate:beginDate+' ' +'00:00:00',
					// endDate:endDate+' '+'23:00:00',
					// // beginDate,
					// // endDate,
        // },
        {
          responseType: 'arraybuffer',
        }
      )
			console.log(res)
      const url = window.URL.createObjectURL(
        new Blob([res], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        })
      )
      const a = document.createElement('a')
      console.log(url)
      a.href = url
      a.download = '机器人外设成本'
      a.click()
      this.downloadLoading = false
    },

    // 点击删除时触发
    deleteRow(index, row) {
      console.log(index, row.robotPeripheralId);
      this.$confirm('此操作将删除该行数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          const { data: res } = await this.$http.get(
            '/system/robotPeripheral/delRobotPeripheral',
            {
              params: {
                robotPeripheralId: row.robotPeripheralId
              }
            }
          )
          console.log(res);
          if (res.state == '200') {
            this.$message({
              type: 'success',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 1000
            })
            this.initTableData()
          } else {
            this.$message({
              type: 'error',
              message: res.msg,
              offset: 100,
              center: true,
              duration: 3000
            })
          }
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },


    /**
     * 获取机器人id列表
     */
    async getRobotIdList () {
      const { data: res } = await this.$http.get('/system/robot/getRobotIdList')
      // console.log(res)
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }
      this.robotOptions = res.data
    },

    // 查询机器人毛利率统计图数据
    async getSpotGrossProfitMarginListStatistical () {
      var startData = null
      var endData = null

      if (this.searchStartEndDate.length !== 0) {
        startData = formatDateTime(
          this.searchStartEndDate[0],
          this.selectDateState
        )
        endData = formatDateTime(
          this.searchStartEndDate[1],
          this.selectDateState
        )
      }

      const { data: res } = await this.$http.get(
        '/system/operateGrossProfit/getSpotGrossProfitMarginListStatistical',
        {
          params: {
            ...this.searchValidateForm,
            type: this.selectDateState,
            startData,
            endData
          }
        }
      )
      // console.log(res);
      if (res.state != 200) {
        this.$message({
          message: '获取机器人列表失败！',
          type: 'error'
        })
        return
      }

      this.drawEcharts()
    },

    /** 搜索表单中选项变化方法 start */
    // 公司选项变化时触发
    async onChangeCompany (companyId) {
      console.log('???');
      var res = await this.getScenicList(companyId)
      var scenicOptions = res.data


      this.scenicOptions = scenicOptions


      // 如果返回的景区列表为空，则直接返回
      if (scenicOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/scenicList 接口返回的景区列表为空"
        // })
        this.validateForm.scenicSpotId = ''
        return
      }

      var isHasScenic = false
      scenicOptions.forEach((item) => {
        if (item.SCENIC_SPOT_ID == this.validateForm.scenicSpotId) {
          isHasScenic = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则景区，则为搜索景区选择框设置默认值
      var scenicSpotId = scenicOptions[0].SCENIC_SPOT_ID /* 获取第一个景区id */
      if (!!companyId && !isHasScenic) {
        this.validateForm.scenicSpotId = scenicSpotId
      }
    },

    // 景区选项变化时触发
    async onChangeSpot (spotId) {
      var res = await this.getCompanyList(spotId)
      // console.log(res.data);
      var companyOptions = res.data

      // 如果返回的公司列表为空，则直接返回
      if (companyOptions.length == 0) {
        // this.$message({
        //   type: "info",
        //   message: "/system/subscriptionInformation/companyList 接口返回的公司列表为空"
        // })
        this.validateForm.peripheralCompanyId = ''
        return
      }

      var isHasCompany = false
      companyOptions.forEach((item) => {
        if (item.COMPANY_ID == this.validateForm.peripheralCompanyId) {
          isHasCompany = true
          return
        }
      })

      // 如果选择框内容不为空并且未选则公司，则为搜索公司选择框设置默认值
      if (!!spotId && !isHasCompany) {
        this.validateForm.peripheralCompanyId =
          companyOptions[0].COMPANY_ID
      }
    },
    /** 搜索表单中选项变化方法 end */

    // 重置
    onReset () {
      // 重新获取表单
      this.getSearchValidateForm()
      this.currentPage = 1

      this.selectDateState = '3'
      this.dateType = 'daterange'
      this.searchStartEndDate = ''

      // 重新获取表格数据
      this.initTableData()
      // 重新获取图表数据
      // this.getSpotGrossProfitMarginChartList()
      // 重新获取搜索公司列表
      this.getCompanyList()
      // 重新获取搜索景区列表
      this.getScenicList()
    },

    // 导出报表（导出柱状图）
    onExportReport () {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReport')
        exportReportEle.href = this.myChart.getDataURL()
        // exportReportEle.click()
      })
    },

    // 导出报表（导出折线图）
    onExportReportLine () {
      this.$nextTick(() => {
        var exportReportEle = document.getElementById('exportReportLine')
        exportReportEle.href = this.myLineChart.getDataURL()
        console.log(exportReportEle)
        // exportReportEle.click()
      })
    },

    // 点击查询数据按钮时触发
    onSearch (formName) {
			console.log(formName)
			var that = this
			console.log(this.$refs[formName])
      this.$refs[formName].validate((valid) => {
				console.log(valid)
        if (valid) {
          that.showTable = true
          // console.log(this.searchValidateForm);
          that.initTableData()
          // this.getSpotGrossProfitMarginChartList()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 接口导出Excel表格
    exportExcel () {
      this.$confirm('确认导出Excel表格?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          this.downloadLoading = true
          await this.getExportTableData()
          this.$message({
            type: 'success',
            message: '已成功导出Excel表格！',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
        .catch(() => {
          this.downloadLoading = false
          this.$message({
            type: 'info',
            message: '已取消导出Excel表格',
            offset: 100,
            center: true,
            duration: 1000
          })
        })
    },

    // 新增
    onAdd () {
      this.dialogVisible = true
      this.dialogState = 'add'
      this.dialogTitle = '新增'
    },

    // 点击编辑时触发
    editRow (index, row) {
      console.log(index, row)

      // 显示 dialog 弹窗
      this.dialogVisible = true // 是否显示编辑dialog对话框
      this.dialogState = 'edit'
      this.dialogTitle = '编辑'

      this.validateForm = { ...row }
    },

    // dialog 取消
    dialogClose () {
      this.dialogVisible = false
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },

    // dialog 右上角关闭按钮
    handleClose (done) {
      done() // 关闭dialog对话框
      this.$refs.validateForm.resetFields() // 重置表单（清空无效）
      this.getValidateForm() // 将validateForm对象中各个属性清空
    },
	// 文件状态改变时的钩子，添加文件、上传成功和上传失败时都会被调用
	handleChange(file, fileList) {
	  this.fileTemp = file.raw
	
	  // 导入判断
	  if (this.fileTemp) {
	    if (
	      this.fileTemp.type ==
	        'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' ||
	      this.fileTemp.type == 'application/vnd.ms-excel'
	    ) {
	      this.importExcel(this.fileTemp)
	    } else {
	      this.$message({
	        type: 'warning',
	        message: '附件格式错误，请删除后重新上传！'
	      })
	    }
	  } else {
	    this.$message({
	      type: 'warning',
	      message: '请上传附件！'
	    })
	  }
	},
	
	// 文件超出个数限制时的钩子
	handleExceed(files, fileList) {
	  this.$message.warning(
	    `当前限制选择 1 个文件，本次选择了 ${
	      files.length
	    } 个文件，共选择了 ${files.length + fileList.length} 个文件`
	  )
	},
		
	// 文件列表移除文件时的钩子
	handleRemove(file, fileList) {
	  this.fileTemp = null
	},
    // 文件上传之前执行
    handleBeforeUpload (file) {
      this.importLoading = true
    },

    // 文件上传成功后执行
    handleImportSuccess () {
      this.importLoading = false
      this.$message({
        type: 'success',
        message: '已成功导入Excel表格！',
        offset: 100,
        center: true,
        duration: 1000
      })
			that.initTableData()
    },
	 async importExcel(obj){
		let _this = this
		// 通过DOM取文件数据
		this.file = obj
		var rABS = false //是否将文件读取为二进制字符串
		var f = this.file
		var reader = new FileReader()
		//if (!FileReader.prototype.readAsBinaryString) {
		FileReader.prototype.readAsBinaryString = function(f) {
		  var binary = ''
		  var rABS = false //是否将文件读取为二进制字符串
		  var pt = this
		  var wb //读取完成的数据
		  var outdata
		  var reader = new FileReader()
		  reader.onload = function(e) {
		    var bytes = new Uint8Array(reader.result)
		    var length = bytes.byteLength
		    for (var i = 0; i < length; i++) {
		      binary += String.fromCharCode(bytes[i])
		    }
		    var XLSX = require('xlsx')
		    if (rABS) {
		      wb = XLSX.read(btoa(fixdata(binary)), {
		        //手动转化
		        type: 'base64'
		      })
		    } else {
		      wb = XLSX.read(binary, {
		        type: 'binary'
		      })
		    }
		    outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]],{range:1,defval:''}) //outdata就是你想要的东西
		    // console.log(wb.Sheets[wb.SheetNames[0]])
		    this.da = [...outdata]
		    let arr = []
		    this.da.map((v) => {
					console.log(v)
		      let obj = {
		        createDate: v['创建时间'],
		        scenicSpotName: v['景区名称'],
		        peripheralName: v['外设名称'],
		        peripheralPrice: v.['外设价格'],
		        peripheralQuantity: v['外设数量'],
		        peripheralCompany: v['外设单位'],
		        remarks: v['备注']
		      }
		      arr.push(obj)
		    })
				console.log(arr)
		    // 将 arr 赋值给 tableData，来更新表格数据
		    this.tableData = arr
				
				
				// console.log(this.tableData)
		    return arr
		  }
		  reader.readAsArrayBuffer(f)
		}
		// const { data: res } = await this.$http.get(
		//   '/system/robotPeripheral/importExcel',
		//   {
		//     params: {
		//       ...this.tableData,
		//     }
		//   }
		// )
		// // console.log(res);
		// if (res.state != 200) {
		//   this.$message({
		//     message: '上传失败！',
		//     type: 'error'
		//   })
		//   return
		// }
		// // console.log(rABS)
		// if (rABS) {
		//   reader.readAsArrayBuffer(f)
		// } else {
		//   reader.readAsBinaryString(f)
		// }
	},
    // dialog提交
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.dialogState == 'add') {
            // 新增机器人外设成本
            console.log('新增机器人外设成本')
            this.addTableData()

          } else if (this.dialogState == 'edit') {
            // 编辑工厂发货信息
            console.log('编辑机器人外设成本')
            this.editTableData()
          }

          this.dialogVisible = false
          this.$refs.validateForm.resetFields() // 重置表单（清空无效）
          this.getValidateForm() // 将validateForm对象中各个属性清空
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // currentPage 改变时会触发
    onCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.initTableData()
    },

    // pageSize 改变时会触发
    onSizeChange (pageSize) {
      // console.log(pageSize);
      this.pageSize = pageSize
      this.initTableData()
    },

    // 搜索表单设备状态改变触发
    onChangeSearchEquipmentStatus (type) { },

    // 提交表单设备状态改变触发
    onChangeEquipmentStatus (type) {
      console.log(type)
    },

    // 提交表单机器人编号改变触发
    onChangeRobotId (robotId) {
      console.log(robotId)
    },

    // 点击关闭设备统计dialog时触发
    handleCloseStatistic () {
      this.statisticDialogVisible = false
    },

    // 切换 tab 时触发
    handleTabClick (tab, event) {
      console.log(tab, event)
    },

    /**
     * 画Echarts图表(柱状图)
     */
    drawEcharts () {
      this.myChart = this.$echarts.init(document.getElementById('myChart'))

      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        legend: {
          data: ['机器人利用率/%', '同比/%', '环比/%'],
          top: 0 // 把legend放到柱状图上面
        },
        xAxis: {
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          axisPointer: {
            type: 'shadow'
          }
        },
        yAxis: [
          {
            name: '机器人利用率/%',
            type: 'value',
            axisLabel: {
              formatter: '{value} 元'
            }
          },
          {
            name: '同比环比/%',
            type: 'value',
            axisLabel: {
              formatter: '{value} %'
            }
          }
        ],
        series: [
          {
            name: '机器人利用率/%',
            data: [120, 200, 150, 80, 70, 110, 130],
            type: 'bar'
          },
          {
            name: '同比/%',
            type: 'line',
            yAxisIndex: 1,
            data: [120, 200, 150, 80, 70, 110, 130]
          },
          {
            name: '环比/%',
            type: 'line',
            yAxisIndex: 1,
            data: [20, 120, 200, 100, 80, 50, 150]
          }
        ]
      }

      this.myChart.setOption(this.option)

      window.addEventListener('resize', () => {
        this.myChart.resize()
      })
    },

    // 日期选项值发生变化时触发
    // onDateChange () {
    //   // console.log(this.searchValidateForm.selectDateState);
    //   if (this.selectDateState == '3') {
    //     // 按月查询
    //     this.dateType = 'daterange'
    //   } else if (this.selectDateState == '2') {
    //     // 按月查询
    //     this.dateType = 'daterange'
    //   } else if (this.selectDateState == '1') {
    //     // 按年查询
    //     this.dateType = 'daterange'
    //   }
    // },

    // 当表格的排序条件发生变化的时候回触发改事件
    onSortChange ({ column, prop, order }) {
      console.log(column, prop, order)
      if (prop == 'grossProfit') {
        // 毛利
        if (order == 'descending') {
          this.tableSort = '1,desc'
        } else if (order == 'ascending') {
          this.tableSort = '1,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'runningAmount') {
        // 流水金额
        if (order == 'descending') {
          this.tableSort = '2,desc'
        } else if (order == 'ascending') {
          this.tableSort = '2,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'comprehensiveCost') {
        // 台均综合成本
        if (order == 'descending') {
          this.tableSort = '3,desc'
        } else if (order == 'ascending') {
          this.tableSort = '3,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'robotGrossProfit') {
        // 台均毛利
        if (order == 'descending') {
          this.tableSort = '4,desc'
        } else if (order == 'ascending') {
          this.tableSort = '4,asc'
        } else {
          this.tableSort = ''
        }
      } else if (prop == 'netProfit') {
        // 净利润
        if (order == 'descending') {
          this.tableSort = '5,desc'
        } else if (order == 'ascending') {
          this.tableSort = '5,asc'
        } else {
          this.tableSort = ''
        }
      }

      this.initTableData()
    },

    // 合计行
    getSummaries (param) {
      const { columns, data } = param
      const sums = []
      columns.forEach((column, index) => {
        if (index === 0) {
          sums[index] = '合计'
          return
        } else if (index == 1) {
          sums[index] = '1'
        } else if (index == 2) {
          sums[index] = '2'
        } else if (index == 3) {
          sums[index] = '3'
        }
      })

      return sums
    }
  },
  components: {
    // DownloadExcel: JsonExcel,
  },
  computed: {
    // activeFixed() {
    //   return (value) => {
    //     // 冻结左侧“合作主体、景区名称、机器人编号”
    //     if (value == 'SCENIC_SPOT_NAME' || value == 'robotCode') {
    //       return 'left'
    //     }
    //   }
    // }

    tableContentValue () {
      return (row, value) => {
        // console.log(row, value)
        var result = ''
        if (
          value == 'cost' ||
          value == 'comprehensiveCost' ||
          value == 'netProfit' ||
          value == 'yearOnYear' ||
          value == 'monthOnMonth' ||
          value == 'grossProfitMargin' ||
          value == 'netInterestRate'
        ) {
          result = `${(row[value] * 100).toFixed(2)}%`
        } else {
          result = row[value]
        }
        return result
      }
    },

    tableSortable () {
      return (value) => {
        var result = ''
        if (
          value == 'runningAmount' ||
          value == 'comprehensiveCost' ||
          value == 'grossProfit' ||
          value == 'robotGrossProfit' ||
          value == 'netProfit'
        ) {
          result = 'custom'
        } else {
          result = false
        }

        return result
      }
    }
  }
}
</script>

